.scroll-top {
   position:fixed;
   bottom:0;
   margin-bottom: 10px;
   right:1%;
   z-index:100;
   background: #f2f3f2;
   font-size:12px;
   border-top-left-radius:3px;
   border-top-right-radius:3px;
   cursor: pointer;
}

.scroll-top a:link,.scroll-top a:visited {
  color:#222;
}



