.modal-body {
  padding: 5px !important;
}
.modal-content {
  border-radius: 0;
}
.modal-dialog img {
  text-align: center;
  margin: 0 auto;
}
.controls {
  width: 50px;
  display: block;
  font-size: 11px;
  padding-top: 8px;
  font-weight: bold;
}
.next {
  float: right;
  text-align: right;
}
/*override modal for demo only*/
.modal-dialog {
  max-width: 500px;
  padding-top: 90px;
}
@media screen and (min-width: 768px) {
  .modal-dialog {
    width: 500px;
    padding-top: 90px;
  }
}



